body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
    padding: 2rem 4rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 7vh;
    font-family: 'Inconsolata', monospace;
}
h2 {
    font-size: 2.2rem;
}

nav {
    font-size: 2.4rem;
    letter-spacing: .2rem;
}

li {
    list-style: none;
}

.btn {
    display: inline-block;
    text-decoration: none;
    padding: 5px;
}

#slide {
    all: unset;
    -webkit-transition: color .3s ease-in-out;
    transition: color .3s ease-in-out;
    position: relative;
}

#slide:hover {
    cursor: pointer;
    color: #EEF4D4;
}

#slide::before {
    cursor: pointer;
    color: #EEF4D4;
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: .15rem;
    width: 100%;
    background-color: #EEF4D4;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transition: -webkit-transform .5s .1s;
    transition: -webkit-transform .5s .1s;
    transition: transform .5s .1s;
    transition: transform .5s .1s, -webkit-transform .5s .1s;

}
#slide:hover::before {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); 
}

@media (max-width: 56.25em){
    header {
        padding: 1rem 1rem 0;
    }
}
.card-container {
    background-color: #4D9DE0;
    border-radius: .8rem;
    
    background-size: cover;
    background-position: center;

    display: flex;
    justify-content: center;
    align-items: center;
    
    height: 13rem;
    width: 13rem;
    padding: .2rem;
    margin: .2rem;
    border: .18rem solid #EEF4D4;

    -webkit-transition: all 1.5s .3s ease-in;

    transition: all 1.5s .3s ease-in;
} 

.card-container:hover {
    cursor: pointer;
}

@media (max-width: 75em){
    .card-container {
        height: 13rem;
        width: 13rem;
    }
}

@media (max-width: 56.25em){
    .card-container {
        height: 12rem;
        width: 12rem;
    }
}

@media (max-width: 37.5em){
    .card-container {
        height: 10.5rem;
        width: 10.5rem;
    }
}

@media (min-width: 112.5em){
    .card-container {
        height: 15rem;
        width: 15rem;;
    }
}


/*Dog*/

.bg {
    background-color: #FA7268;
    height: 100vh;
    padding: 150px 0;
  }
  
  #dog {
    width: 250px;
    height: 300px;
    margin: 0 auto;
  }
  
  .mask {
    overflow: hidden;
  }
  
  .head {
    width: 150px;
    height: 67px;
    border-radius: 0px 0px 0px 100px;
    background-color: #B47A52;
    position: relative;
    z-index: 2;
  }
  .head::before {
    content: " ";
    width: 0;
    height: 0;
    border-left: 23px solid transparent;
    border-right: 23px solid transparent;
    border-bottom: 67px solid #B47A52;
    position: absolute;
    top: 0px;
    left: 127px;
  }
  
  .mask-head {
    overflow: hidden;
    width: 150px;
    height: 67px;
    border-radius: 0px 0px 0px 100px;
  }
  
  .nose {
    background-color: #EEF4D4;
    width: 58px;
    height: 100px;
    -webkit-transform: rotate(-17deg);
            transform: rotate(-17deg);
    position: relative;
    top: -10px;
    /*  opacity: 0;*/
  }
  .nose::after {
    background-color: #2F434E;
    content: '';
    width: 20px;
    height: 20px;
    border-radius: 100%;
    position: absolute;
    top: 0px;
    left: 7px;
  }
  
  .eye {
    width: 10px;
    height: 10px;
    background-color: #20201E;
    border-radius: 50px;
    position: absolute;
    top: 15px;
    left: 77px;
  }
  
  .ear {
    background-color: #8D4F3A;
    width: 57px;
    height: 103px;
    border-radius: 0 0 50px 50px;
    right: 0;
    position: absolute;
    top: 0;
    z-index: 2;
  }
  
  .body {
    width: 81px;
    height: 200px;
    position: relative;
    left: 92px;
    background-color: #3d484e;
    z-index: 1;
    float: left;
  }
  .body::after {
    background-color: #EF4023;
    content: '';
    width: 35px;
    height: 20px;
    position: absolute;
    top: 0px;
    right: 0px;
  }
  
  .mask-body {
    width: 81px;
    height: 200px;
    overflow: hidden;
    position: relative;
  }
  .mask-body::before {
    background-color: #242B31;
    content: '';
    width: 192px;
    height: 196px;
    border-radius: 100%;
    position: absolute;
    bottom: -100px;
    left: 44px;
  }
  
  .leg {
    width: 0;
    height: 0;
    border-left: 44px solid transparent;
    border-right: 44px solid transparent;
    border-bottom: 132px solid #EEF4D4;
    position: absolute;
    left: -44px;
    bottom: 0;
  }
  
  .foot {
    width: 0;
    height: 0;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-bottom: 20px solid #EEF4D4;
    position: absolute;
    left: -20px;
    bottom: 0;
  }
  
  .back-body {
    width: 60px;
    height: 200px;
    background-color: #3d484e;
    position: relative;
    overflow: hidden;
    right: 50px;
    z-index: 1;
    -webkit-transform: skewX(15deg);
            transform: skewX(15deg);
    float: right;
  }
  .back-body::after {
    background-color: #242B31;
    content: '';
    width: 192px;
    height: 196px;
    border-radius: 100%;
    position: absolute;
    top: 110px;
    left: -21px;
  }
  .back-body::before {
    background-color: #4D9DE0;
    content: '';
    width: 35px;
    height: 20px;
    position: absolute;
    top: 0px;
    right: 0px;
  }
  
  .mask-tail {
    width: 50px;
    height: 150px;
    position: relative;
    left: 40px;
    top: 50px;
    float: right;
    overflow: hidden;
  }
  .mask-tail::before {
    content: " ";
    width: 0;
    height: 0;
    border-left: 43px solid transparent;
    border-right: 43px solid transparent;
    border-bottom: 150px solid #3d484e;
    position: absolute;
    bottom: 0px;
    left: 6px;
  }
  .mask-tail::after {
    content: " ";
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 14px solid transparent;
    border-bottom: 36px solid #FFFFFF;
    position: absolute;
    top: -2px;
    left: 38px;
  }
  
  .shirt {
    width: 50px;
    height: 5px;
    border-radius: 90px 20px 0 40px;
    background-color: #EF4023;
    position: absolute;
    top: -14px;
    right: 123px;
    z-index: 0;
    -webkit-transform: rotate(-17deg);
            transform: rotate(-17deg);
  }


  /*Loading...*/

  .spinner {
    margin: 100px auto 0;
    width: 70px;
    text-align: center;
  }
  
  .spinner > div {
    width: 18px;
    height: 18px;
    background-color: #4D9DE0;
  
    border-radius: 100%;
    display: inline-block;
    -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
    animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  }
  
  .spinner .bounce1 {
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
  }
  
  .spinner .bounce2 {
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
  }
  
  @-webkit-keyframes sk-bouncedelay {
    0%, 80%, 100% { -webkit-transform: scale(0) }
    40% { -webkit-transform: scale(1.0) }
  }
  
  @keyframes sk-bouncedelay {
    0%, 80%, 100% { 
      -webkit-transform: scale(0);
      transform: scale(0);
    } 40% { 
      -webkit-transform: scale(1.0);
      transform: scale(1.0);
    }
  }
.cardsContainer {

    grid-template-rows: repeat(4, 12rem);
    grid-template-columns: repeat(5, 12rem);
    grid-gap: 1.8rem;

}
.container {
    display: flex;
    flex-direction: column;
    position: relative;
    height: 100vh;
    width: 100vw;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}
.content {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 1rem;
    height: 100%;
}
.load {
    position: absolute;
    height: 100%;
    width: 100vw;
    overflow: hidden;
}
.hide {
    display: none;
}

@media (max-width: 75em){
    .cardsContainer {
        grid-template-rows: repeat(4, 13rem);
        grid-template-columns: repeat(5, 13rem);
        grid-gap: 3.2rem;
    }
    .content {
        padding-top: 1rem;
    }
}

@media (max-width: 56.25em){
    .cardsContainer {
        grid-template-rows: repeat(4, 12rem);
        grid-template-columns: repeat(5, 12rem);
        grid-gap: 3rem;
    }
}

@media (max-width: 50em){ /* 820px:16px=50em  1em=16px */
    .cardsContainer {
        grid-template-rows: repeat(5, 12rem);
        grid-template-columns: repeat(4, 12rem);
        grid-gap: 2.5rem;
    }
    .content {
        padding-top: 2.5rem;
        height: 100%;
    }
}

@media (max-width: 37.5em){
    .cardsContainer {
        grid-template-rows: repeat(5, 10.5rem);
        grid-template-columns: repeat(4, 10.5rem);
        grid-gap: 1.3rem;
    }
}


@media (max-width: 32.5em){
    .cardsContainer {
        grid-template-rows: repeat(5, 10.5rem);
        grid-template-columns: repeat(3, 10.5rem);
        grid-gap: 1.3rem;
    }
    .container {
        height: 100%;
    }
}

@media (min-width: 112.5em){
    .cardsContainer {
        grid-template-rows: repeat(4, 15rem);
        grid-template-columns: repeat(5, 15rem);
        grid-gap: 5.5rem;
    }
    .content {
        padding-top: 2.5rem;
        height: 100%;
    }
}
